import React, { Component } from 'react';


export default class ToggleButton extends Component {

    render() {
        
        return (
            <button 
            className={this.props.toggler_classname}
            onClick={this.props.btn_toggler_onclick}
            type="button"
            data-navbar-toggler="#navbar-wrapper"
            >
                <span className="hamburger-box">
                    <span className="hamburger-inner"></span>
                </span>
            </button>
        );
    }
}

