import React, { Component } from 'react';

import { Col, Row } from 'react-bootstrap';

import CardTextItem from './CardTextItem.js';
import AboutMeCardData from '../../../Data/AboutMeCardData.js';
import AboutMeForm from './AboutMeForm.js';

export default class AboutMeCard extends Component {
    render() {
        const CardTextItems = AboutMeCardData.map(item => {
            return (

                <CardTextItem
                    name={item.name}
                    delay={item.delay}
                    key={item.name}
                />

            );
        });

        

        return (
                <div className="about-me-card">
                    <div className="about-me-card-background">
                        <Row className="justify-content-center">
                            <Col lg={12} className="about-me-card-information">
                                <ul className="about-me-card-list">
                                    {CardTextItems}
                                </ul>
                            </Col>
                        </Row>


                        <AboutMeForm/>
                    </div>
                </div>
        )
    }
}
