import React, { Component } from 'react'

import { Container } from 'react-bootstrap';

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default class WhatIDoCarouselItem extends Component {
    render() {
        return (
            <Container>
                <div className="whatido-card">
                    <div className="whatido-card-title">
                        <FontAwesomeIcon className="whatido-card-icon" icon={this.props.icon}/>
                        <h3 className="whatido-card-subtitle text-shadow">
                            {this.props.subtitle}
                        </h3>
                    </div>
                    <p className="whatido-card-text">
                        {this.props.text}
                    </p>
                </div>
            </Container>
        )
    }
}
