import React, { Component } from 'react';
import ToggleButton from './ToggleButton.js';
import NavbarList from './NavbarList.js';

export default class NavbarWrapper extends Component {


    render() {
        return (
            <div 
            className={this.props.navbar_wrapper_classname}
            id="navbar-wrapper"
            >
                <ToggleButton
                toggler_classname={this.props.btn_toggler_classname}
                btn_toggler_onclick={this.props.btn_toggler_onclick}
                />
                <NavbarList/>
            </div>
        )
    }
}

