import React, { Component } from 'react';

export default class ScrollIcon extends Component {

    constructor(props) {
        super(props);
        this.state = {
            scroll_icon_classname: 'scroll-downs scroll-thing active'
        }
    }

    render() {

        return (
            <div className={this.state.scroll_icon_classname}>
                <div className="mousey">
                    <div className="scroller"></div>
                </div>
            </div>
        )
    }
}
