import React, { Component } from 'react';

import { Col } from 'react-bootstrap';

import LinkButton from '../../../LinkButton.js';

export default class HomeWorksCard extends Component {
    render() {
        return (
            <Col xs={9} sm={6} md={4} lg={3}>
                <div className="home-works-card-col">
                    <div className="home-works-card">
                        <div className="home-works-card-img">
                            <img src={this.props.img} alt="" />
                        </div>
                        <div className="home-works-card-body">
                            <h4 className="home-works-card-title">{this.props.title}</h4>
                            <p className="home-works-card-subtitle">
                                {this.props.subtitle} <br />
                                <span>click on me</span>
                            </p>
                        </div>
                    </div>
                    <div className="works-link-1">
                        <LinkButton
                            link='/works/project'
                            link_text='view this project'
                        />
                    </div>
                    <div className="works-link-2">
                        <LinkButton
                            link='/works'
                            link_text='more projects'
                            onClick={this.props.onClick}
                        />
                    </div>
                </div>
            </Col>
        )
    }
}
