import React, { Component } from 'react';

import { Container, Row, Col } from 'react-bootstrap';

import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import WorksProject from './WorksProject/WorksProject.js';

import WorksCube from './WorksCube.js';
import WorksText from './WorksText.js';
import HomeWorksCard from './HomeWorksCard.js';
import WorksCardData from '../../../Data/WorksCardData.js';

import LinkButton from '../../../LinkButton.js';


export default class HomeWorksSection extends Component {

    constructor(props) {
        super(props);
        this.state =  {
            title: 'WWWWWW'
        }
    }

    render() {
        const WorksCards = WorksCardData.map(item => {
            return (
                <>
                    <HomeWorksCard
                        img={item.img}
                        title={item.title}
                        subtitle={item.subtitle}
                        onClick={this.onClick}
                    />
                </>
            );
        });

        return (
            <section className="home-works">
                <Container>
                    <h2 className="home-works-title text-shadow">My Works</h2>
                    <p className="home-works-subtitle">
                        Take a look at some projects of mine
                    </p>

                    <Row className="justify-content-center">
                        <Col lg={6}>
                            <WorksCube />
                        </Col>
                        <Col lg={6}>
                            <WorksText
                                works_text={'At Daniel.inc my passion is to provide innovative, highly functional, visually appealing and feature-rich website designs and mobile applications as well as the digital marketing programs that drive business growth. I hold myselve to the highest standards of creative and technical excellence. Every client, regardless of the size or scope of project, can expect nothing but the best designs, customer service and professionalism from the experienced, dedicated and marketing savvy staff at Daniel.inc.'}
                            />
                        </Col>
                        {WorksCards}
                        <WorksText
                            works_text={'There is no such a big quantity of good programmists in Uzbekistan and Tashkent. So here it is "Daniel.inc" At Daniel.inc i can provide you with good quality, design and highly functional Web Sites, Web Applications and Landing Pages. A great website is only the starting point. Daniel.inc provides solutions that drive results.I hold myselve to the highest standards of creative and technical excellence. Chekout my latest works in Web Design and Web programming'}
                        />
                        <LinkButton
                            link={'/works'}
                            link_text={'view all works'}
                        />
                    </Row>
                </Container>

                <Router>
                    <Switch>
                        <Route exact path="/works/project" component={() => <WorksProject text={this.state.title}/>}/>
                    </Switch>
                </Router>
            </section>
        )
    }
}
