import React, { Component } from 'react';
import NavbarListItem from './NavbarListItem.js';
import NavbarListItemData from '../Data/NavbarListItemData.js';


export default class NavbarList extends Component {
    
    render() {
        const NavbarListItems = NavbarListItemData.map(item => {
            return (
                <NavbarListItem
                name={item.name}
                link={item.link}
                key={item.name}
                />
            );
        });
        return (
            <ul className="navbar-list">
                {NavbarListItems}
            </ul>
        )
    }
}
