import React, { Component } from 'react';
import './App.scss';
import './styles/libs/css/hamburgers.min.css';
import 'bootstrap/dist/css/bootstrap.min.css';

import Header from './Components/Header/Header.js';
import Footer from './Components/Footer/Footer.js';
import Loader from './Components/Loader/Loader.js';

// Home AbutUs WhatWeDo AboutMe Contacts


export default class App extends Component {

  constructor (props) {
    super(props);
    this.state = {
      loader_classname: 'loader'
    };
  }

  render() {
    document.body.onload = () => {
      document.body.style.overflowY = 'hidden';
      setTimeout(() => {
        if (this.state.loader_classname !== 'loader done' ) {
          this.setState({loader_classname: 'loader done'});
        }
      }, 1000);
      
      setTimeout(() => {
          document.body.style.overflowY = 'unset';
        }, 5000)
    }
    

    return (

      <div className="App">
        <Loader 
        loader_classname={this.state.loader_classname}
        />
        <Header/>
        <Footer/>
      </div>
    );
  }
}

