import React, { Component } from 'react'

import { Container, Row, Col } from 'react-bootstrap';

import NavbarLogo from '../Header/NavbarLogo.js';

export default class Footer extends Component {
    render() {
        return (
            <footer className="footer">
                <Container>
                    <div className="footer-border">
                        <Row className="justify-content-center">
                            <Col lg={6}>
                                <div className="footer-title">
                                    gdaniel.inc
                                </div>
                            </Col>
                            <Col lg={6}>
                                <div className="footer-text">
                                    <NavbarLogo />
                                    <p className="footer-text-content">
                                        Lorem ipsum dolor sit amet consectetur adipisicing elit.
                                        Est at deserunt quod! Voluptate, commodi laboriosam nostrum tempora quos reprehenderit
                                        quod quibusdam obcaecati accusamus sunt unde sint eos, officia facilis.
                                        Nulla quo temporibus quibusdam alias consequuntur.
                                    </p>
                                </div>
                            </Col>
                        </Row>
                    </div>
                </Container>
            </footer>
        )
    }
}
