import React, { Component } from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default class SocialMediaListItem extends Component {
    render() {
        return (
            <li>
               <a href={this.props.link} className={this.props.class_name}>
                       <FontAwesomeIcon icon={this.props.fa}/>
                </a> 
            </li>
        )
    }
}
