import React, { Component } from 'react';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import WhatIDoCarouselItemsData from '../../../Data/WhatIDoCarouselItemsData.js';
import WhatIDoCarouselItem from './WhatIDoCarouselItem.js';

export default class WhatIDoCarousel extends Component {
    render() {
        const WhatIDoCarouselItems = WhatIDoCarouselItemsData.map(item => {
            return (
                <div>
                    <WhatIDoCarouselItem
                    icon={item.icon}
                    subtitle={item.subtitle}
                    text={item.text}
                    />
                </div>
            )
            
        });

        return (
            <OwlCarousel items={1} loop autoplay>
                {WhatIDoCarouselItems}
            </OwlCarousel>
        )
    }
}
