import React, { Component } from 'react';

import ScrollIcon from './ScrollIcon.js';
import ScrollArrow from './ScrollArrow.js';

export default class ScrollItem extends Component {


    render() {
        const ScrollItem = () => {
            if (window.innerWidth < 1025) {
                return (
                    <div>
                        <ScrollArrow />
                    </div>
                    
                );
            }
            else if (window.innerWidth > 1024) {
                return (
                    <div>
                        <ScrollIcon />
                    </div>
                );
            }
        }

        return (
            <>
                <ScrollItem/>
            </>
        )
    }
}
