import React, { Component } from 'react';
import SocialMediaListData from '../Data/SocialMediaListData.js';
import SocialMediaListItem from './SocialMediaListItem';

export default class SocialMediaList extends Component {

    render() {
        const SocialMediaListItems = SocialMediaListData.map(item => {
            return (
                
                    <SocialMediaListItem
                    key={item.key}
                    link={item.link}
                    class_name={item.class_name}
                    fa={item.fa}
                    />
            );
        });

        return (
            <div className={this.props.social_media_list_classname}>
                <ul className="social-media-list">
                    {SocialMediaListItems}
                </ul>
            </div>
        )
    }

}
