import React, { Component } from 'react';
import { Container } from 'react-bootstrap';
import Typist from 'react-typist';

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHtml5, faCss3, faJs, faGulp, faGithub,  faLess, faSass, faPython, faReact } from '@fortawesome/free-brands-svg-icons';

import ScrollItem from './ScrollItem.js';

export default class HomeHeaderContent extends Component {
    render() {
        return (
            <div>
                <div className="tetris-box">
                    <div className="tetris-box-line">
                        <div className="elem elem-1"><FontAwesomeIcon icon={faHtml5}/></div>
                    </div>
                    <div className="tetris-box-line">
                        <div className="elem elem-1"><FontAwesomeIcon icon={faCss3}/></div>
                        <div className="elem elem-2"><FontAwesomeIcon icon={faGulp}/></div>
                    </div>
                    <div className="tetris-box-line">
                        <div className="elem elem-1"><FontAwesomeIcon icon={faJs}/></div>
                        <div className="elem elem-2"><FontAwesomeIcon icon={faGithub}/></div>
                        <div className="elem elem-3"><FontAwesomeIcon icon={faReact}/></div>
                    </div>
                    <div className="tetris-box-line">
                        <div className="elem elem-1"><FontAwesomeIcon icon={faLess}/></div>
                        <div className="elem elem-2"><FontAwesomeIcon icon={faPython}/></div>
                    </div>
                    <div className="tetris-box-line">
                        <div className="elem elem-1"><FontAwesomeIcon icon={faSass}/></div>
                    </div>
                    
                </div>
                <Container>
                    <div className="header-content">
                        <div className="header-text">
                                <h1 className="header-title">
                                    
                                        hello humans!
                                    
                                </h1>

                            <div className="header-subtitle">
                            <Typist>
                                <Typist.Delay ms={4000}/>    
                                    Here is my Portfolio. It seems to me you'll like it
                            </Typist>    
                            </div>
                        </div>

                        

                        <ScrollItem/>

                    </div>
                </Container>
            </div>
        )
    }
}
