import React, { Component } from 'react';

import { Container, Row, Col } from 'react-bootstrap';
 
export default class WorksProject extends Component {
    render() {
        return (
            <section className="works-project">
                <Container>
                    <Row className="justify-content-center">
                        <Col lg={6}>
                            <div className="works-project-img">
                                <img src={this.props.img} alt=""/>
                            </div>
                        </Col>
                        <Col lg={6}>
                            <p className="works-project-text">
                                {this.props.text}
                            </p>
                        </Col>
                    </Row>
                </Container>
            </section>
        )
    }
}
