import React, { Component } from 'react'

export default class AboutMeForm extends Component {
  constructor(props) {
    super(props);
    this.state = { name: '', email: '' };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ name: event.target.name, email: event.target.email });
  }

  handleSubmit(event) {
    alert('Name: ' + this.state.name, '/nEmail: ' + this.state.email);
    event.preventDefault();
  }

  render() {
    return (
      <form action="submit" className="about-me-form" onSubmit={this.handleSubmit}>
        <input type="text" className="about-me-form-input" placeholder="Your Name" value={this.state.name} onChange={this.handleChange} />
        <input type="email" className="about-me-form-input" placeholder="Your Email" value={this.state.email} onChange={this.handleChange} />
        <button type="submit" className="about-me-form-submit" value="Submit">
          <a href="#!">
            Send
          </a>
        </button>
      </form>
    )
  }
}
