import React, { Component } from 'react';
import Typist from 'react-typist';

export default class AboutMeCardItem extends Component {
    render() {
        return (
            <li className="about-me-card-list-item">
                    <Typist>
                        <Typist.Delay ms={this.props.delay}/>
                        {this.props.name}
                    </Typist>
            </li>
        )
    }
}
